
.slideshow-container {
    /* max-width: 200px; */
    position: relative;
    margin: 30px auto; 
    height: 300px;
    /* border-bottom: 2px solid rgb(231, 228, 228); */
  }
  
  .prev, .next {
    cursor: pointer;
    position: absolute;
    width: auto;
    padding: 4px;
    /* color: white; */
    font-weight: bold;
    font-size: 23px;
    transition: 0.6s ease;
    border-radius: 0 3px 3px 0;
    -webkit-user-select: none;
    user-select: none;
    top: 50%;
    margin: 10px;
  }
  .prev-card {
    cursor: pointer;
    position: absolute;
    padding: 30px 10px;
    font-weight: bold;
    font-size: 27px;
    transition: 0.6s ease;
    border-radius: 0 3px 3px 0;
    -webkit-user-select: none;
    user-select: none;
    top: 50%;
    left:0;
    margin: 10px;
    box-shadow: 0 3px 10px rgb(0 0 0 / 20%);
    background: #fff;
  }
  .next-card{
    cursor: pointer;
    position: absolute;
    padding: 30px 10px;
    font-weight: bold;
    font-size: 27px;
    transition: 0.6s ease;
    border-radius: 0 3px 3px 0;
    -webkit-user-select: none;
    user-select: none;
    top: 50%;
    right:0;
    margin: 10px;
    box-shadow: 0 3px 10px rgb(0 0 0 / 20%);
    background: #fff;
  }
    .next {
    right: 0;
    border-radius: 3px 0 0 3px;
  }
  
  /* On hover, add a black background color with a little bit see-through */
  .prev:hover, .next:hover {
    background-color: #f2f2f2;
    color: #000;

    /* border-radius: 55%; */
  }
  
  /* Caption text */
  .text {
    color: #f2f2f2;
    font-size: 15px;
    padding: 8px 12px;
    position: absolute;
    bottom: 8px;
    width: 100%;
    text-align: center;
  }
  
  /* Number text (1/3 etc) */
  .numbertext {
    color: #f2f2f2;
    font-size: 12px;
    padding: 8px 12px;
    position: absolute;
    top: 0;
  }
  
  /* The dots/bullets/indicators */
  .dot {
    cursor: pointer;
    height: 15px;
    width: 15px;
    margin: 0 2px;
    background-color: #bbb;
    border-radius: 50%;
    display: inline-block;
    transition: background-color 0.6s ease;
  }
  
  .active, .dot:hover {
    background-color: #717171;
  }
  
  /* Fading animation */
  .fade {
    animation-name: fade;
    animation-duration: 1.5s;
  }
  
  @keyframes fade {
    from {opacity: .4} 
    to {opacity: 1}
  }
  
  /* On smaller screens, decrease text size */
  @media only screen and (max-width: 300px) {
    .prev, .next,.text {font-size: 11px}
  }
  